import React, {Component} from 'react';
import {Redirect, Route, Switch} from "react-router-dom";
import PropTypes from "prop-types";

import './style.css';
import Header from './Header/Header';
import Footer from './Footer/Footer';
import MainPage from "./MainPage/MainPage";
import ServicePage from "./ServicePage/ServicePage";
import SmoothScroll from "./SmoothScroll";

export default class App extends Component {
  render() {
    const {url} = this.props.routeProps.match;

    return (
        <div className="app">
          <Header headerInverse={this.props.headerInverse}
                  baseUrl={url}
                  data={this.props.marginals}/>

          <Switch>
            <Route path={`${url}/services`}
                   render={() => <ServicePage {...this.props.content.services}/>}/>

            <Route path={`${url}/main`}
                   render={() => <MainPage {...this.props.content.main}/>}/>

            <Redirect from={url} to={`${url}/main`}/>
          </Switch>

          <Footer {...this.props.marginals}/>

          <SmoothScroll history={this.props.routeProps.history}/>
        </div>
    );
  }
}

App.propTypes = {
  headerInverse: PropTypes.bool.isRequired,
  routeProps: PropTypes.object.isRequired,

  content: PropTypes.shape({
    services: PropTypes.object.isRequired,
    main: PropTypes.object
  }),
  marginals: PropTypes.object.isRequired
};



